*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  min-height: 100%;
  box-sizing: border-box;
  font-size: 62.5%;
  overflow-x: hidden;
}

@media only screen and (max-width: 22.5em) {
  html {
    font-size: 56.25%;
  }
}

body {
  color: #d0d6f9;
  font-family: Bellefair, serif;
  font-weight: 400;
  line-height: 1.6;
}

.grid-container {
  grid-template-columns: repeat(2, min-content);
  display: grid;
}

@media only screen and (max-width: 62.5em) {
  .grid-container {
    grid-template-columns: minmax(min-content, 1fr);
  }
}

.nav {
  justify-content: flex-start;
  align-items: center;
  padding: 3rem 0 0 3rem;
  display: flex;
}

@media only screen and (max-width: 62.5em) {
  .nav {
    justify-content: space-between;
    padding: 0;
  }
}

.nav__icon {
  width: 5rem;
  height: 5rem;
  margin: 0 2rem;
}

@media only screen and (max-width: 62.5em) {
  .nav__icon {
    margin: 6px 4rem 0;
  }
}

@media only screen and (max-width: 40.625em) {
  .nav__icon {
    margin: 4rem 4rem 0;
  }
}

.nav__decoline {
  height: 1px;
  width: 35vw;
  z-index: 10;
  background-color: #fff3;
  position: relative;
  transform: translateX(4rem);
}

@media only screen and (max-width: 81.25em) {
  .nav__decoline {
    width: 33vw;
  }
}

@media only screen and (max-width: 62.5em) {
  .nav__decoline {
    display: none;
  }
}

.nav__button {
  display: none;
}

@media only screen and (max-width: 40.625em) {
  .nav__button {
    z-index: 1000;
    cursor: pointer;
    flex-direction: column;
    gap: 1rem;
    margin-top: 1rem;
    display: flex;
    position: fixed;
    top: 4rem;
    right: 4rem;
  }
}

.nav__button__icon-1, .nav__button__icon-2, .nav__button__icon-3 {
  width: 3.5rem;
  height: 4px;
  background-color: #d0d6f9;
  display: inline-block;
}

.nav__button__icon-1, .nav__button__icon-3 {
  transform-origin: center;
  transition: all .3s;
}

.nav__button__icon-1--active {
  transform: rotate(135deg)translate(1rem, -1rem);
}

.nav__button__icon-2--active {
  background-color: #0000;
}

.nav__button__icon-3--active {
  transform: rotate(-135deg)translate(1rem, 1rem);
}

.nav__links {
  width: 60%;
  -webkit-backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  backdrop-filter: blur(15px);
  background-image: linear-gradient(to right, #ffffff0d, #d0d6f91a);
  gap: 5rem;
  padding: 0 4rem 0 13rem;
  display: flex;
  position: relative;
}

.nav__links > * {
  text-decoration: none;
}

@media only screen and (max-width: 81.25em) {
  .nav__links {
    gap: 4rem;
    padding: 0 2rem 0 9rem;
  }
}

@media only screen and (max-width: 68.75em) {
  .nav__links {
    gap: 3rem;
    padding: 0 2rem 0 7rem;
  }
}

@media only screen and (max-width: 62.5em) {
  .nav__links {
    justify-content: space-evenly;
    gap: 0;
    padding: 0;
  }
}

@media only screen and (max-width: 40.625em) {
  .nav__links {
    width: 70%;
    z-index: 999;
    background-image: linear-gradient(#0b0d1780, #d0d6f94d);
    flex-direction: column;
    justify-content: flex-start;
    gap: 2rem;
    padding: 20rem 0 0 5rem;
    transition: all .3s;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    transform: translateX(100%);
  }

  .nav__links--close {
    transform: translateX(100%);
  }

  .nav__links--open {
    transform: translateX(0);
  }
}

.nav__link {
  color: #fff;
  cursor: pointer;
  border-bottom: 3px solid #fff0;
  padding: 3.5rem 0 calc(3.5rem - 3px);
  transition: border-bottom .2s;
}

